import { liveRequestHandler } from "./urls";

const config = {
    method: 'get',
    baseURL: liveRequestHandler,
    responseType: 'json',
    params: {
        request: null
    }
};

const TOPSTOCKS = "TOPSTOCKS";
const MARKETSUMMARY = "MARKETSUMMARY";
const MARKETINFORMATION = "MARKETINFORMATION";
const MARKETCLOSEINFORMATION = "MARKETCLOSEINFORMATION";
const TODAYNEWS = "TODAYNEWS";
const STOCKLIVE = "STOCKLIVE";

export const topStocksApi = { ...config, params: { ...config.params, request: TOPSTOCKS } };
export const marketSummaryApi = { ...config, params: { ...config.params, request: MARKETSUMMARY } };
export const marketInformationApi = { ...config, params: { ...config.params, request: MARKETINFORMATION } };
export const marketCloseInformationApi = { ...config, params: { ...config.params, request: MARKETCLOSEINFORMATION } };
export const todayNewsApi = { ...config, params: { ...config.params, request: TODAYNEWS } };
export const stockLiveApi = { ...config, params: { ...config.params, request: STOCKLIVE } };