/*global cordova, Connection*/
import React, { useState, useEffect } from 'react';
import { Route, Switch, HashRouter, BrowserRouter } from 'react-router-dom';
// import { renderRoutes } from 'react-router-config';
import Loadable from 'react-loadable';
import './App.scss';
import { UserContext, MarketContext } from './context/contexts';
import firebase from './firebase/Firebase';
import { marketInformationApi } from "./api/live";
import axios from 'axios';

const loading = () => <div className="animated fadeIn pt-3 text-center">Loading...</div>;

// Containers
const DefaultLayout = Loadable({
  loader: () => import('./containers/DefaultLayout'),
  loading
});

// Pages
const Login = Loadable({
  loader: () => import('./views/Pages/Login'),
  loading
});

// Chart
const Chart = Loadable({
  loader: () => import('./views/Pages/Chart/LoadChart'),
  loading
});

const Register = Loadable({
  loader: () => import('./views/Pages/Register'),
  loading
});

const Verify = Loadable({
  loader: () => import('./views/Pages/VerifyEmail'),
  loading
});

const Delete = Loadable({
  loader: () => import('./views/Pages/Delete'),
  loading
});

const Profile = Loadable({
  loader: () => import('./views/Pages/Profile/Profile'),
  loading
});

const ErrorPage = Loadable({
  loader: () => import('./views/Pages/Error/ErrorPage'),
  loading
});

const nullUser = {
  uid: null,
  displayName: null,
  email: null,
  emailVerified: false
}

function App() {

  const [currentUser, setCurrentUser] = useState(nullUser);
  const [market, setMarket] = useState("Closed");

  const reloadApp = () => {
    if (!document.URL.match(/^http?:/) && navigator.connection.type != Connection.NONE) {
      window.location.reload();
    }
  };

  const exitApp = () => {
    if (!document.URL.match(/^http?:/)) {
      if (window.confirm("Exit Application")) {
        navigator.app.exitApp();
      }
    }
  }

  const updateMarketStatus = () => {
    axios(marketInformationApi)
      .then((marketInformation) => {
        if ((marketInformation.data).hasOwnProperty('tradeStartsIn')) {
          setMarket("Closed");
          setTimeout(updateMarketStatus, marketInformation.data.tradeStartsIn * 1000);
        } else {
          setMarket("Live");
        }
      });
  }



  useEffect(() => {
    const unsubscribe = firebase.auth().onAuthStateChanged((user) => {
      if (user) {
        setCurrentUser(user);
      } else {
        setCurrentUser(nullUser);
      }
    });
    return () => {
      unsubscribe();
    };
  });


  useEffect(() => {
    updateMarketStatus();
    if (market == "Live") {
      let intervalId = -1;
      intervalId = setInterval(updateMarketStatus, 30000);
      return () => {
        clearInterval(intervalId);
      }
    }
  }, [market]);

  useEffect(() => {
    document.addEventListener('resume', reloadApp, false);
    return () => {
      document.removeEventListener('resume', reloadApp, false);
    }
  });

  useEffect(() => {
    document.addEventListener('backbutton', exitApp, false);
    return () => {
      document.removeEventListener('backbutton', exitApp, false);
    }
  })



  return (
    <UserContext.Provider value={currentUser}>
      <BrowserRouter>
        <Switch>
          <Route exact path="/login" name="Login Page" component={Login} />
          <Route exact path="/register" name="Register Page" component={Register} />
          <Route exact path="/verify" name="Verify Email" component={Verify} />
          <Route exact path="/profile" name="profile" component={Profile} />
          <Route exact path="/error" name="Error" component={ErrorPage} />
          <Route exact path="/chart" name="Chart" component={Chart} />
          <Route exact path="/delete" name="Delete" component={Delete} />
          <MarketContext.Provider value={{ market }}>
            <Route path="/" name="Home" component={DefaultLayout} />
          </MarketContext.Provider>
        </Switch>
      </BrowserRouter>
    </UserContext.Provider>
  );
}


export default App;
